import React from "react";

import BlogPost from "./BlogPost"
import {Link, Route, Routes} from "react-router-dom";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowRight } from '@fortawesome/free-solid-svg-icons'
import { faArrowLeft } from '@fortawesome/free-solid-svg-icons'
import { Carousel } from '@trendyol-js/react-carousel';




export const GearReviews = () => {
    return (
        <div className={"container"}>


            <div className={"text-center my-5"}>
                <h1 className={"fw-bolder"}>Outdoor Pursuits</h1></div>


            <h2>Vehicles Reviewed From an Outdoors Enthusiasts Perspective</h2>
            <p>
                Most car reviews focus on things like brake horse power and torque. Reviews on this website will be
                completely different. Cars will be reviewed based on their most important aspects, including practically,
                sleeping comfort, back seat dining room setup.</p>

            <Carousel show={3.5}
                      slide={3}
                      swiping={true}
                      rightArrow={<FontAwesomeIcon icon={ faArrowRight} size={100} />}
                      leftArrow={<FontAwesomeIcon icon={faArrowLeft} />}>
                <Link to="saab">
                <img className={"img-fluid img-thumbnail"}
                     src={"https://lh3.googleusercontent.com/yogRZAH0bIwrsuoUbK0ekm4v2YoiG0YBW0PWLrcn8R55mdG_G5VrNbT4IktS0sE7QP1g7O1oaTPaNo3TYUFrNqV9NzWF-zW9nqf0qgcd5zl8rbdyBCXQRIWB3uwmNefN-CZs7E8Vzw=w2400"}/>
                </Link>
                <Link to="bmw">
                <img className={"img-fluid img-thumbnail"}
                         src={"https://lh3.googleusercontent.com/LSfUMvMCI-mcSPqcUTsbOoIXRtkfZH_RPtKk_zZX66MmAXj-NpS0VdonnIe4QS0K5SR-bKQgrBh9PWnOilqNEuImBJnZJZ2D15-zvTgcp3eP3ZEayYo2y2mfO57htwF76O8BUyNFGw=w2400"}/>
                </Link>
                <Link to="Orange">
                    <img className={"img-fluid img-thumbnail"}
                         src={"https://lh3.googleusercontent.com/2CCBlD90Cc6x3irB7q50c_rRdE_1SuPxiPZj7PyC_ed6NcEImI6KB4jscrzUA0ZCSsojmY85NWsXOuKJ9tukYK9RucYIGO6KSCEvkDMGhMEr-srhdtaQZvC_Bmd8ZJZbq87KsW0z6w=w2400"}/>
                </Link>
                <Link to="Cooker">
                    <img className={"img-fluid img-thumbnail"}
                         src={"https://lh3.googleusercontent.com/0qcNzr6OUGJ20GnWC7l0dXhX550NuaQhDl7I1Gcqs-pv4G3rjwEviQ6iwbc1RRrcpI7VWqbZXugqQLdHH9MRwxc20pJhz_p_J12iFPORwT_IjmVpg7cqkhx9DnwkYewx3WKqadjg6Q=w2400"}/>
                </Link>
                <Link to="Cube">
                    <img className={"img-fluid img-thumbnail"}
                         src={"https://lh3.googleusercontent.com/1j9l0Wtw8Rd47KOesYxliMcOIzBU49THle0IK-2X9EhgvK_l9PX2VXzoXo_Yt9zd7b0u9ae83gPQNwPMTMX_MXF3rNapeRhXOxnv7nFOuzUrF7WgBpDUfGgGrvy7oFNsrRZ3_IdUOA=w2400"}/>
                </Link>
            </Carousel>






            <Routes>
                <Route path="saab" element={<BlogPost title={"SAAB"} />} />
                <Route path="bmw" element={<BlogPost title={"BMW"} />} />
                <Route path="Orange" element={<BlogPost title={"Orange"} />} />
                <Route path="Cube" element={<BlogPost title={"Cube"} />} />
                <Route path="Cooker" element={<BlogPost title={"Cooker"} />} />
            </Routes>


        </div>)
}